
import { defineComponent } from 'vue'

export default defineComponent({
  props: {
    title: {
      type: String,
      default: ''
    },
    listData: {
      type: Array,
      required: true
    },
    listCount: {
      type: Number,
      default: 0
    },
    propList: {
      type: Array,
      required: true
    },
    showIndexColumn: {
      type: Boolean,
      default: false
    },
    showSelectionColumn: {
      type: Boolean,
      default: false
    },
    page: {
      type: Object,
      default: () => ({ currentPage: 0, pageSize: 10 })
    },
    childrenProps: {
      type: Object,
      default: () => ({})
    },
    showFooter: {
      type: Boolean,
      default: true
    }
  },
  emits: ['selectionChange', 'update:page'],
  setup(props, { emit }) {
    const handleSelectChange = (value: any) => {
      emit('selectionChange', value)
      console.log(value)
    }

    const sizeChange = (pageSize: number) => {
      //覆盖pageSize
      emit('update:page', { ...props.page, pageSize })
    }

    const currentChange = (currentPage: number) => {
      //覆盖currentPage
      emit('update:page', { ...props.page, currentPage })
    }
    return {
      handleSelectChange,
      sizeChange,
      currentChange
    }
  }
})
